<template>
	<div>
		<div class="level">

			<div class="level-left">
				<p class="level-item">
					<strong class="title is-5">show</strong>
					<div class="field">
						<p class="control">
							<span class="select ">
      <select @change="selectChange" v-model="selectValue">
        <option v-for="val in dataSelect" :value="val" ref="opt">{{val}}</option>
      </select>
    </span>
						</p>
					</div>
				</p>
			</div>

			<div class="level-right">
				<div class="level-item">
					<div class="field has-addons" id="line">
						<input class="input is-primary"  v-model="info.content" type="text" placeholder="Search">
						<button class="button is-primary">Search</button>
					</div>
				</div>
			</div>

		</div>

		<table class="table is-bordered ">
			<thead>
				<tr>
					<th>Id</th>
					<th>Name</th>
					<th>Email</th>
					<th>City</th>
					<th>Company</th>
					<th>Job</th>
					<th>Date</th>
				</tr>
			</thead>

			<tbody>
				<tr v-for="(v,i) in dataRow ">
					<th>{{  v.id}}</th>
					<td>{{v.name}}</td>
					<td>{{v.email}}</td>
					<td>{{v.city}}</td>
					<td>{{v.company}}</td>
					<td>{{v.job}}</td>
					<td>{{v.created_at}}</td>
				</tr>
			</tbody>
		</table>
<nav class="pagination is-right  ">
	
	   	<p class="field ">
			<a class="button is-info">
				<span class="icon">
      <i class="fa fa-edit"></i>
    </span>
				<span>Edit</span>
			</a>
			<a class="button is-danger">
				<span class="icon">
      <i class="fa fa-trash-o"></i>
    </span>
				<span></span>
			</a>
			<a class="button is-success">
				<span class="icon is-small">
      <i class="fa fa-eye"></i>
    </span>
				<span>Show</span>
			</a>
		</p>
		
  <ul class="pagination-list ">
		
    
     <a class="pagination-previous " :class="{'is-disabled':index===1}" @click="page(index-1)"></a>
     <a class="pagination-next " :class="{'is-disabled':index===count}" @click="page(index+1)"></a>
      <li>
      	<a class="pagination-link"v-show="count>0 && index>1" @click="page(1)">1</a>
      </li>
    <li v-show="count>3 && index>3">
    	<span class="pagination-ellipsis">&hellip;</span>
    </li>
    <li  v-show="count>0 && index>2" @click="page(index-1)" >
    	<a class="pagination-link button" v-text="index-1"></a>
    </li>
    <li v-show="count>0" @click="page(index)">
    	<a class="pagination-link is-current"  v-text="index"></a>
    </li>
    <li v-show="index<count" @click="page(index+1)">
    	<a class="pagination-link" v-text="index+1"></a>
    </li>
    <li v-show="(index+2)<count">
    	<span class="pagination-ellipsis">&hellip;</span>
    </li>
    <li  v-show="(index+1)<count" @click="page(count)">
    	<a class="pagination-link" v-text="count"></a>
    </li>
    
  </ul>
  
</nav>

	</div>
</template>

<script>
	import axios from 'axios';
	export default {
		data() {
			return {
				data: [],
				dataSelect:[1,5,10,20,30],
				selectValue:5,
				index:1,
				info:{
					 content:'',
					 id:0,
					 index:0
				}
			}
		},
		computed:{
			 dataRow(){
			 	  if (!this.info.content) {
                  return  this.data.slice((this.index-1)*this.selectValue,this.index*this.selectValue)
			 	  	
			 	  } else{
			 	  	return  this.data.filter((val)=>{
			 	  	  	    if(val.name.indexOf(this.info.content)!==-1){
			 	  	  	    	  return val
			 	  	  	    }
			 	  	  })
			 	  }
			 },
			
			 count(){//
			 	  return this.data.length/this.selectValue
			 }
		},
		beforeMount() {
			axios.get('./static/data.json').then((res) => {
				this.data = res.data.data;
			})
		},
		methods:{
			selectChange(){
				  this.page(1);
				  console.log(this.count)
			},
			page(ii){
				 this.index=ii;
			}
		}
	}
</script>

<!--<style  scoped="scoped" src="../../../static/fontawesome.css"></style>-->
<style  scoped="scoped" src="../../../static/bulma.css"></style>
<style type="text/css" scoped="scoped">
	#line {
		display: flex;
		justify-content: flex-end;
	
	}
	
	ul{
		list-style: none;
	}
	li{
		list-style: none;
	}
</style>


